.wrap {
    /*margin: - top: 150px;*/
    -webkit-perspective: 1000px;
            perspective: 1000px;
    -webkit-perspective-origin: 50% 50%;
            perspective-origin: 50% 50%;
    background-image: -webkit-linear-gradient(315deg,#ccc 25%,#ddd 25%,#ddd 50%, #ccc 50%, #ccc 75%,#ddd 75%,#ddd 100%);
    background-image: linear-gradient(135deg,#ccc 25%,#ddd 25%,#ddd 50%, #ccc 50%, #ccc 75%,#ddd 75%,#ddd 100%);
    background-size: 80px 80px;
    border-top: solid 3px #999;
    border-bottom: solid 3px #999;
    /* Background stripes animation */
    -webkit-animation: bganim 3s linear 2s infinite;
            animation: bganim 3s linear 2s infinite;
}

@-webkit-keyframes bganim {
    from {
        background-position: 0px;
    }

    to {
        background-position: 80px;
    }
}

@keyframes bganim {
    from {
        background-position: 0px;
    }

    to {
        background-position: 80px;
    }
}

.cube {
    margin: auto;
    position: relative;
    height: 200px;
    width: 200px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.cube div {
    position: absolute;
    /*padding: 10px;*/
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    opacity: 0.9;
    background-color: #000;
    border: solid 1px #eee;
    color: #fff;
    font: 50px arial;
    -webkit-transition: -webkit-transform 0.2s ease-in;
    transition: -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in;
    transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
}

.front {
    -webkit-transform: translateZ(100px);
            transform: translateZ(100px);
}

.back {
    -webkit-transform: translateZ(-100px) rotateY(180deg);
            transform: translateZ(-100px) rotateY(180deg);
}

.right {
    -webkit-transform: rotateY(-270deg) translateX(100px);
            transform: rotateY(-270deg) translateX(100px);
    -webkit-transform-origin: top right;
            transform-origin: top right;
}

.left {
    -webkit-transform: rotateY(270deg) translateX(-100px);
            transform: rotateY(270deg) translateX(-100px);
    -webkit-transform-origin: left center;
            transform-origin: left center;
}

.top {
    -webkit-transform: rotateX(-270deg) translateY(-100px);
            transform: rotateX(-270deg) translateY(-100px);
    -webkit-transform-origin: top center;
            transform-origin: top center;
}

.bottom {
    -webkit-transform: rotateX(270deg) translateY(100px);
            transform: rotateX(270deg) translateY(100px);
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
}

@-webkit-keyframes rotate {
    from {
        -webkit-transform: rotateX(0deg) rotateY(0deg);
                transform: rotateX(0deg) rotateY(0deg);
    }

    to {
        -webkit-transform: rotateX(360deg) rotateY(360deg);
                transform: rotateX(360deg) rotateY(360deg);
    }
}

@keyframes rotate {
    from {
        -webkit-transform: rotateX(0deg) rotateY(0deg);
                transform: rotateX(0deg) rotateY(0deg);
    }

    to {
        -webkit-transform: rotateX(360deg) rotateY(360deg);
                transform: rotateX(360deg) rotateY(360deg);
    }
}

.cube {

}
.cubeAnimation {
    -webkit-animation: rotate 10s infinite linear;
            animation: rotate 10s infinite linear;
}
.cube:hover {
    /*animation: normal;*/
}
.wrap:hover .front {
    -webkit-transform: translateZ(130px);
            transform: translateZ(130px);
}
.wrap:hover .front {
    -webkit-transform: translateZ(200px);
            transform: translateZ(200px);
}

.wrap:hover .back {
    -webkit-transform: translateZ(-200px) rotateY(180deg);
            transform: translateZ(-200px) rotateY(180deg);
}

.wrap:hover .right {
    -webkit-transform: rotateY(-270deg) translateZ(100px) translateX(100px);
            transform: rotateY(-270deg) translateZ(100px) translateX(100px);
}

.wrap:hover .left {
    -webkit-transform: rotateY(270deg) translateZ(100px) translateX(-100px);
            transform: rotateY(270deg) translateZ(100px) translateX(-100px);
}

.wrap:hover .top {
    -webkit-transform: rotateX(-270deg) translateZ(100px) translateY(-100px);
            transform: rotateX(-270deg) translateZ(100px) translateY(-100px);
}

.wrap:hover .bottom {
    -webkit-transform: rotateX(270deg) translateZ(100px) translateY(100px);
            transform: rotateX(270deg) translateZ(100px) translateY(100px);
}


.boomFront {
    -webkit-transform: translateZ(700px);
            transform: translateZ(700px);
}
.boomBack {
    -webkit-transform: translateZ(-700px) rotateY(180deg);
            transform: translateZ(-700px) rotateY(180deg);
}

.boomRight {
    -webkit-transform: rotateY(-270deg) translateZ(700px) translateX(100px);
            transform: rotateY(-270deg) translateZ(700px) translateX(100px);
}

.boomLeft {
    -webkit-transform: rotateY(270deg) translateZ(700px) translateX(-700px);
            transform: rotateY(270deg) translateZ(700px) translateX(-700px);
}

.boomTop {
    -webkit-transform: rotateX(-270deg) translateZ(700px) translateY(-700px);
            transform: rotateX(-270deg) translateZ(700px) translateY(-700px);
}

.boomBottom {
    -webkit-transform: rotateX(270deg) translateZ(700px) translateY(700px);
            transform: rotateX(270deg) translateZ(700px) translateY(700px);
}




